.form {
    display: flex;
  }
  
  .form input {
    flex: 1; 
    font-size: 1.25rem;
    outline: none;
    border: none;
    border-bottom: 1px solid #c7fac5;
  }
  
  .create-button {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 1rem;
    background: #38bb84;
    border-radius: 3px;
    color: white;
    font-weight: 600;
    cursor: pointer;
  }
  
  .create-button:hover {
    background: #4db84d;
  }